import React from 'react'
import photo from '../assets/about.png'
import proc2 from '../assets/proc2.png'
import proc1 from '../assets/proc1.png'
import proc3 from '../assets/proc3.png'
import Typist from 'react-typist';
import arrR from "../assets/arrowRight.png";
import {Route, NavLink} from 'react-router-dom'
export default Proc => {

    return(
        <>
            <Typist className='aboutTag' startDelay={350}
            cursor={{
                show: true,
                blink: true,
                element: '|',
                hideWhenDone: true,
                hideWhenDoneDelay: 400,
            }}
            >
                {/*<div className='aboutTag'>Прокрастинатор</div>*/}
                Прокрастинатор
            </Typist>
            <NavLink className='next' to='/homestat' >
                Следующий проект
                <img src={arrR}/>
            </NavLink>
            <div className='pageProjectBlock'>
                <div className='leftPageBlock'>
                    <div className='pargh'>Проект мобильного приложения, которое призвано помочь людям в осознании и переосмыслении проблемы прокрастинации.</div>
                    <div className='pargh'>Был разработан интерфейс с системой тестирования и сбором статистики, показывающий советы на основе завершенеия кратких опросов.</div>
                    <div className='pargh'>Созданный проектный буклет содержит информацию о стилистическом решении, основных аспектах исследования и этапах работы.</div>
                    <img src={proc2} className='photoAbout spaceTop'/>
                </div>
                <div className='rightPageBlock'>
                    <img src={proc1} className='photoAbout'/>
                    <div className='pargh spaceTop2'>Работа выполнялась в составе группы из четырех человек: Иванов Максим, <a className='hrefToPerson' href='https://matehype.github.io/zz/' target='_blank'>Заозерский Даниил</a>, Кириллов Валентин, Кудинова Татьяна.</div>
                    <div className='pargh'>Год реализации: 2020</div>
                    <div className='pargh'>Программное обеспечение: Figma, Adobe Photoshop, Adobe Illustrator, Adobe InDesign</div>
                </div>
            </div>
            <img src={proc3} className='spaceTop end'/>
        </>
    )
}