import React from 'react'
import photo from '../assets/elec1.png'
import Typist from 'react-typist';
import proc3 from "../assets/elec2.png";
import arrR from "../assets/arrowRight.png";
import {Route, NavLink} from 'react-router-dom'
export default Elec => {

    return(
        <>
            <Typist className='aboutTag' startDelay={350}
                    cursor={{
                        show: true,
                        blink: true,
                        element: '|',
                        hideWhenDone: true,
                        hideWhenDoneDelay: 400,
                    }}
            >Elektroskandia RUS</Typist>
            <NavLink className='next' to='/psychology' >
                Следующий проект
                <img src={arrR}/>
            </NavLink>
            <div className='pageProjectBlock'>
                <div className='leftPageBlock'>
                    <div className='pargh'>Проект редизайна интернет-магазина компании по продаже электротехники.</div>
                    <div className='pargh'>В  ходе работы заказчик предоставил творческую свободу над визуальной составляющей, контролируя сохранение функционала.</div>
                    <div className='pargh'>Результат оправдал ожидания заказчика и далее был передан в разработку верстки.</div>
                    <div className='pargh'>Заказчиком является филиал международной компаниии “Elektroskandia RUS”.</div>
                    <div className='pargh'>Год реализации: 2019</div>
                    <div className='pargh'>Программное обеспечение: Adobe Photoshop, Adobe Illustrator</div>
                </div>
                <div className='rightPageBlock'>
                    <img src={photo} className='photoAbout'/>
                </div>
            </div>
            <img src={proc3} className='spaceTop end'/>
        </>
    )
}