import React from 'react'
import photo from '../assets/dota1.png'
import Typist from 'react-typist';
import proc3 from "../assets/dota2.png";
import arrR from '../assets/arrowRight.png'
import {Route, NavLink} from 'react-router-dom'
export default Dota => {

    return(
        <>
            <Typist className='aboutTag' startDelay={350}
                    cursor={{
                        show: true,
                        blink: true,
                        element: '|',
                        hideWhenDone: true,
                        hideWhenDoneDelay: 400,
                    }}
            >Dota 2 Market</Typist>
            <NavLink className='next' to='/elektoskandiarus' >
                Следующий проект
                <img src={arrR}/>
            </NavLink>
            <div className='pageProjectBlock'>
                <div className='leftPageBlock'>
                    <div className='pargh'>Проект редизайна сайта торговой площадки виртуальных игровых ценностей «Dota 2 Market».</div>
                    <div className='pargh'>Для визуальной составляющей было решено использовать блоки скругленной формы. Крупные прописные шрифты на кнопках были заменены на более спокойные начертания. Использована фирменная цветовая гамма с дополнением в виде темно-серого цвета.</div>
                    <div className='pargh'>Важной частью проекта было сохранение исходных элементов фильтрации и поиска.</div>
                    <div className='pargh'>Год реализации: 2020</div>
                    <div className='pargh'>Программное обеспечение: Figma, Adobe Photoshop</div>
                </div>
                <div className='rightPageBlock'>
                    <img src={photo} className='photoAbout'/>
                </div>
            </div>
            <img src={proc3} className='spaceTop end'/>
        </>
    )
}