import React from 'react'
import './About.scss'
import photo from '../assets/about.png'
import Typist from 'react-typist';
export default About => {

    return(
        <>
            <Typist className='aboutTag' startDelay={350}
                    cursor={{
                        show: true,
                        blink: true,
                        element: '|',
                        hideWhenDone: true,
                        hideWhenDoneDelay: 400,
                    }}
            >Обо мне</Typist>
            <div className='pageBlock'>
                <div className='leftPageBlock'>
                    <div className='pargh'>Меня зовут Максим Иванов.</div>
                    <div className='pargh'>Я живу в городе Санкт-Петербурге и занимаюсь web-разработкой и дизайном интерфейсов на протяжении 2-х лет.</div>
                    <div className='pargh'>Мне удалось поработать над проектами как крупных компаний, так и небольших стартапов.</div>
                    <div className='pargh'>Специализируюсь на создании пользовательских интерфейсов с помощью инструментов Figma и Adobe Photoshop. Владею всеми этапами процесса, от идеи и исследования области до реализации дизайна. Также я могу присоединиться к команде и сосредоточиться на решении одной из задач.</div>
                    <div className='pargh'>Имею опыт работы в верстке печатной продукции и разработке интерфейсов мобильных приложений.</div>
                </div>
                <div className='rightPageBlock'>
                        <img src={photo} className='photoAbout'/>
                </div>
            </div>
        </>
    )
}