import { useEffect } from "react";
import { useLocation } from "react-router-dom";

export default function MomentScroll() {
    const { key } = useLocation();
    console.log(useLocation())

    useEffect(() => {
        window.scrollTo(0, 0);
        if(document.querySelector('.arrowTop')){
            document.querySelector('.arrowTop').style.opacity='0'
        }
    }, [key]);

    return null;
}