import React from 'react'
import photo from '../assets/psy1.png'
import Typist from 'react-typist';
import proc3 from "../assets/psy2.png";
import {Route, NavLink} from 'react-router-dom'
import arrR from "../assets/arrowRight.png";
export default Psy => {

    return(
        <>
            <Typist className='aboutTag' startDelay={350}
                    cursor={{
                        show: true,
                        blink: true,
                        element: '|',
                        hideWhenDone: true,
                        hideWhenDoneDelay: 400,
                    }}
            >Сайт психолога</Typist>
            <NavLink className='next' to='/procrastinator' >
                Следующий проект
                <img src={arrR}/>
            </NavLink>
            <div className='pageProjectBlock'>
                <div className='leftPageBlock'>
                    <div className='pargh'>Личный сайт психолога, представляющий собой одностраничный лендинг с навигацией и формой записи.</div>
                    <div className='pargh'>Целью проекта стало создание дополнительной площадки для привлечения новых клиентов, с чем идеально справляется целевая страница.</div>
                    <div className='pargh'>Заказчиком является практикующий психолог Татьяна Олейник.</div>
                    <div className='pargh'>Год реализации: 2021</div>
                    <div className='pargh'>Программное обеспечение: Figma, Adobe Photoshop, Adobe Illustrator, HTML/CSS/JavaScript</div>
                </div>
                <div className='rightPageBlock'>
                    <img src={photo} className='photoAbout'/>
                </div>
            </div>
            <img src={proc3} className='spaceTop end'/>
        </>
    )
}