import React from 'react'
import photo from '../assets/homestat1.png'
import Typist from 'react-typist';
import proc3 from "../assets/homestat2.png";
import proc2 from '../assets/homestat3.png'
import arrR from '../assets/arrowRight.png'
import {Route, NavLink} from 'react-router-dom'
export default Homestat => {

    return(
        <>
                <Typist className='aboutTag' startDelay={350}
                        cursor={{
                            show: true,
                            blink: true,
                            element: '|',
                            hideWhenDone: true,
                            hideWhenDoneDelay: 400,
                        }}
                >Homestat</Typist>
                <NavLink className='next' to='/dota2market' >
                    Следующий проект
                    <img src={arrR}/>
                </NavLink>


            <div className='pageProjectBlock'>
                <div className='leftPageBlock'>
                    <div className='pargh'>Работа над проектом для IT-старатапа заключалась в разработке интерфейса сайта, который представляет собой веб-ресурс, собирающий информацию с счетчиков потребления электричества и воды в жилых помещениях и выводящий информацию о расходах каждым подключенным к системе устройством.</div>
                    <div className='pargh'>Работа выполнялась в команде с <a className='hrefToPerson' href='https://matehype.github.io/zz/' target='_blank'>Заозерским Даниилом</a>.</div>
                    <div className='pargh'>Год реализации: 2021</div>
                    <div className='pargh'>Программное обеспечение: Figma, Adobe Photoshop, Adobe Illustrator</div>
                </div>
                <div className='rightPageBlock'>
                    <img src={photo} className='photoAbout'/>
                </div>
            </div>
            <img src={proc3} className='spaceTop'/>
            <img src={proc2} className='spaceTop2 end'/>
        </>
    )
}