import React from 'react'
import './Home.scss'
import Block from "./Block/Block";
import photo from '../assets/photoHome.png'
import Typist from 'react-typist';

export default Home =>{

    return(
        <>
            <img src={photo} className='homePhoto'/>
            <Typist className='homeTag' startDelay={350} stdTypingDelay={75} avgTypingDelay={125}
                    cursor={{
                        show: true,
                        blink: true,
                        element: '|',
                        hideWhenDone: true,
                        hideWhenDoneDelay: 400,
                    }}
            >Web-дизайнер Максим Иванов</Typist>
            <div className='homeText'>Занимаюсь web-разработкой и дизайном интерфейсов</div>
            <div className='homeProjectsTag'>Проекты</div>
            <Block/>
        </>
    )
}